.svg {
  display: inline-block;
  fill: $red;
  width: 36px;
  height: 36px;
  &-sm {
    width: 16px;
    height: 16px;
  }
  &-md {
    width: 48px;
    height: 48px;
  }
  &-lg {
    width: 80px;
    height: 80px;
    @include d(1199) {
      width: 64px;
      height: 64px;
    }
  }
}
